<template>
  <div class="separator">
    <div class="info">
      <h3>本周任务进度</h3>
      <a @click="func_show_all" class="show_all kizk_show_all">{{!allShowFlag ? "查看所有" : "缩略形式"}}</a>
    </div>
    <div class="search">
      <i class='bx bx-search'></i>
      <input type="text" placeholder="待开发..">
    </div>
  </div>
  <div class="analytics">
    <div class = "item" v-for="k in filteredAnalytics" :key="k.id">
      <div class="pgs">
        <div class="info">
          <h5>{{k.name}}</h5>
          <p>{{k.okr}}</p>
        </div>
        <div class="pgs-bar" role="pgsbar" aria-valuenow="75" aria-valuemin="0"
             aria-valuemax="100"></div>
      </div>
      <i :class="k.icon"></i>
    </div>
  </div>


</template>

<script>
import {reactive, ref} from "vue";
import $ from "jquery";
import {useStore} from "vuex";

export default {
  name: "AnalyticsCop",
  computed: {
    filteredAnalytics() {
      if (this.allShowFlag) {
        return this.allAnalytics.itemLists;
      } else {
        return this.allAnalytics.itemLists.filter(k => k.id <= 4);
      }
    }
  },
  setup(){
    const allAnalytics = reactive({
      itemLists: [
        // {id: 1, name: "整理题目", okr: "a",  icon: "bx bx-map-pin"},
        // {id: 2, name: "公众号文章发布", okr: "b", icon: "bx bx-user-voice"},
        // {id: 3, name: "健身", okr: "c", icon: "bx bxs-plane-land"},
        // {id: 4, name: "每日冥想", okr: "d", icon: "bx bxs-castle"}
      ]
    })
    const lv_icons = ref([
       "bx bx-map-pin", "bx bx-user-voice", "bx bxs-plane-land", "bx bxs-castle",
    ]);
    const lv_this_week_finished_misson = ref({});
    const store = useStore();
    const get_length_str = (time_length) => {

      let lv_task_length = time_length;
      let lv_task_length_hours = Math.floor(lv_task_length / 3600);
      let lv_task_length_min = Math.floor((lv_task_length % 3600) / 60);
      let lv_task_length_sec = lv_task_length % 60;
      if (lv_task_length < 7200) {
        // 小于 120 分钟
        let lv_task_length_min = Math.floor(lv_task_length / 60);
        if (lv_task_length_sec === 0) {
          return `${lv_task_length_min}分钟`;
        } else {
          return `${lv_task_length_min}分钟${lv_task_length_sec}秒`;
        }
      } else {
        // 大于等于 120 分钟
        if (lv_task_length_sec === 0) {
          if(lv_task_length_min === 0)
            return `${lv_task_length_hours}小时`;
          return `${lv_task_length_hours}小时${lv_task_length_min}分钟`;
        } else {
          return `${lv_task_length_hours}小时${lv_task_length_min}分钟${lv_task_length_sec}秒`;
        }
      }
    };

    const func_show_info = () => {
      $.ajax({
        url: "https://app6866.acapp.acwing.com.cn/sakura/getjson/",
        type: "GET",
        headers: {
          'Authorization': "Bearer " + store.state.user.access,
        },
        data: {
          'uid': store.state.user.uid,
        },
        success(resp) {
          if (resp.result === 'success') {
            // TODO
            // 渲染对应的画面
            // console.log(resp.json)
            let task_info = resp['json']['study_tasks_info'];
            lv_this_week_finished_misson.value = task_info['this_week_finished_mission'];
            for(let k in lv_this_week_finished_misson.value)
            {
              let lv_study_mission_length = lv_this_week_finished_misson.value[k];
              let lv_study_mission_length_str = get_length_str(lv_study_mission_length);
              let lv_mission_name = k;
              allAnalytics.itemLists.push({id: allAnalytics.itemLists.length+1, name: lv_mission_name, okr: lv_study_mission_length_str, icon: lv_icons.value[Math.floor(Math.random() * lv_icons.value.length)]});
            }
            console.log(allAnalytics.itemLists);
          }
        }
      });
    };
    func_show_info();
    const allShowFlag = ref(false);
    const func_show_all = () => {
      allShowFlag.value = !allShowFlag.value;

      const planningElement = document.querySelector('.analytics');
      if (allShowFlag.value === true) {
        console.log(allAnalytics.itemLists.length);
        if(allAnalytics.itemLists.length > 4)
          planningElement.style.maxHeight = '600px';
        else
          planningElement.style.maxHeight = '300px';
      } else {
        planningElement.style.maxHeight = '300px';
      }
    };
    return {allAnalytics, allShowFlag, func_show_all}
  }

}
</script>

<style scoped>
p{
  padding: 0;
  margin: 0;
}
h5{
  margin: 0;
}
.show_all{
  cursor: pointer;
}
.analytics{
  transition: height 3s ease-in-out;
  overflow: hidden;
  max-height: 300px;
  overflow-y: auto;
  animation: move 1.2s
}
</style>
