<template>
  <main>
    <header>
      <MenuBtnCop :btnStatus="true"></MenuBtnCop>
    </header>
        <div class="separator">
          <h4>⏰ 今日打卡排名</h4>
        </div>
    <div class="rank_list">
    <table class="table table-bordered table-striped kizk_table">
      <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">b站用户名</th>
        <th scope="col">今日打卡时间</th>
        <th scope="col">本周打卡次数</th>
      </tr>
      </thead>


      <tbody>
      <tr v-for="(userinfo, index) in lv_this_day_clock_in_rank_list" :key="userinfo.id">
        <th scope="row">{{index + 1}}</th>
        <td> <img :src="userinfo.photo" class="bili-photo"> &nbsp; {{userinfo.uname}}</td>
        <td>{{userinfo.clock_in_time}}</td>
        <td>{{userinfo.week_clock_in_count}}</td>
      </tr>

      </tbody>
    </table>
    </div>
  </main>
</template>

<script>
import $ from "jquery";
import {useStore} from "vuex";
import {ref} from "vue";
import MenuBtnCop from "@/components/MenuBtnCop";

export default {
  name: "RankListView.vue",
  components:{
    MenuBtnCop,
  },
  setup(){
    const getTodayDate = () => {
      const today = new Date();
      const year = today.getFullYear();
      const month = (today.getMonth() + 1).toString().padStart(2, '0'); // 加 1 是因为月份从 0 开始计数，padStart 补零
      const day = today.getDate().toString().padStart(2, '0');
      let lv_today_str = `${year}${month}${day}`;
      let lv_today_num = parseInt(lv_today_str);
      return lv_today_num;
    }
    const store = useStore();
    const lv_this_day_clock_in_count = ref();
    const lv_this_day_clock_in_rank_list = ref([{}]);
    const func_get_rank_list = () => {
      // 获取今日的日期 如 20240604 的格式
      let lv_cur_day = getTodayDate();
      $.ajax({
        url: "https://app6866.acapp.acwing.com.cn/sakura/allinfo/",
        type: "GET",
        headers: {
          'Authorization': "Bearer " + store.state.user.access,
        },
        data: {
          'info_id': lv_cur_day,
        },
        success(resp) {
          if(resp.result=='success')
          {
            const lv_all_info = JSON.parse(resp['info']);
            lv_this_day_clock_in_count.value = lv_all_info['clock_in_count']; // 初始化今日排名
            lv_this_day_clock_in_rank_list.value = lv_all_info['clock_in_rank_list'];
          }          if(resp.result=='success')
          {
            const lv_all_info = JSON.parse(resp['info']);
            lv_this_day_clock_in_count.value = lv_all_info['clock_in_count']; // 初始化今日排名
            lv_this_day_clock_in_rank_list.value = lv_all_info['clock_in_rank_list'];
          }



        },


      });
    };
    func_get_rank_list();
    return {lv_this_day_clock_in_count, lv_this_day_clock_in_rank_list}
  }
}
</script>

<style scoped>
.bili-photo{
  width:30px;
  border-radius: 50%;
}
.rank_list{
  max-height: 600px;
  overflow-y: auto;
  overflow: scroll;
}
</style>
